@import "../../Icons/arrow-down.scss";
.Dropping_checkbox_wrap{
    width: 266px;
    z-index: 9;
}

.Dropping_checkbox_head{
    font-size: 12px;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 17.5px;
    position: relative;
    cursor: pointer;
    font-family: 'Montserrat';
}


.Dropping_checkbox_cont{
    display: none;
}
.Dropping_checkbox_div{
    margin-bottom: 15px;
}
.Dropping_checkbox_wrap input[type="checkbox"]{
    display: none;

    & + label{
        background-color: white;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0em;
        text-align: left;
        color: rgba(31, 32, 65, 0.5);
        
        &::before {
            content:'';
            display: inline-block;
            margin-right: 10px;
            position: relative;
            text-align: center;
            text-indent: 0px;
            width: 20px;
            height: 20px;
            border: 1px solid rgba(31, 32, 65, 0.25);
            box-sizing: border-box;
            border-radius: 4px;
            border-image: initial;
            vertical-align: middle;
            cursor: pointer;
        }

    }

}
.Dropping_checkbox_wrap input[type="checkbox"]:checked{
   
    & + label{
        background-color: white;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0em;
        text-align: left;
        color: rgba(31, 32, 65, 0.5);
        &::before {
            content: "expand_more";
            font-family: 'Material Icons';
            font-size: 18px;
            display: inline-block;
            margin-right: 10px;
            font-style: normal;
            font-weight: 400;
            width: 20px;
            height: 20px;
            background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            color: #BC9CFF;
            border: 1px solid #BC9CFF ;
            vertical-align: middle;
            cursor: pointer;
            }

    }

}
.Dropping_checkbox_head .arrow-down{
    top:0;
    padding-top: 0px;
    margin-top: 0px;
    width: 17.5px;
    height: 17.5px;
    margin-right: 13px
}