@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: normal;
 
	src: local('Material Icons'),
		url("../assets/fonts/MaterialIcons/MaterialIcons-Regular.ttf") format("truetype"),
		url("../assets/fonts/MaterialIcons/MaterialIcons-Regular.woff") format("woff"),
		url("../assets/fonts/MaterialIcons/MaterialIcons-Regular.svg") format("svg"),
		url("../assets/fonts/MaterialIcons/MaterialIcons-Regular.eot") format("eot"); 
};

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
  
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
  
    /* Support for IE. */
    font-feature-settings: 'liga';
  }
 